<template>
  <el-card shadow="hover" :id="floor">
    <div slot="header">
      <el-row>
        <el-col :span="4">{{ floor }}</el-col>
        <el-col :span="9">{{ username }}</el-col>
        <el-col :span="9">{{ time }}</el-col>
        <el-col :span="2">
          <el-link :underline="false" :href="formattedReply">
            {{ formattedReply }}
          </el-link>
        </el-col>
      </el-row>
    </div>
    <div v-html="comment"></div>
  </el-card>
</template>

<script>
export default {
  props: {
    floorDetail: {},
  },
  data() {
    return {
      floor: this.floorDetail.floor,
      username: this.floorDetail.username,
      time: this.floorDetail.time,
      reply: this.floorDetail.reply,
      comment: this.floorDetail.comment,
    };
  },
  computed: {
    // 拼接楼层跳转字符串
    formattedReply() {
      return this.reply ? `#${this.reply}` : "";
    },
  },
};
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 2vh;
  font-size: 14px;
}
</style>