<template>
  <div class="main">
    <div class="title">
      <div class="row">
        <h3>[新番讨论]感觉mygo的直播楼需要一个专门用来给长颈鹿交流的专楼。</h3>
        <el-link
          href="https://bbs.nga.cn/read.php?tid=38466050"
          target="_blank"
          :underline="false"
          >前往原帖
        </el-link>
      </div>
    </div>
    <div class="content">
      <div v-for="item in floorDetail" :key="item.floor">
        <div v-if="item.reply == ''">
          <Floor :floorDetail="item"></Floor>
        </div>
        <div v-else>
          <el-popover placement="top" trigger="hover">
            <p>{{ getPopoverContent(item.reply).username }}：</p>
            <div
              v-html="getPopoverContent(item.reply).comment"
              class="reply"
            ></div>
            <div slot="reference"><Floor :floorDetail="item"></Floor></div>
          </el-popover>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Floor from "@/components/posts/Floor.vue";
// 直播直播楼数据
import floorDetail from "@/assets/38466050";
export default {
  components: {
    Floor,
  },
  data() {
    return {
      floorDetail,
    };
  },
  methods: {
    // 获得回复的楼层内容
    getPopoverContent(reply) {
      return this.floorDetail.find((item) => item.floor === reply);
    },
  },
};
</script>

<style lang="less" scoped>
.row {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 2vw;
  h3 {
    flex: 1;
  }
}
.reply {
  max-width: 60vw;
}
</style>